<template>
  <div id="app">
    <app-title></app-title>
    <app-album :imgs="albumImgs" @clickImg="handleAlbumClick"></app-album>
    <transition name="fade">
      <app-gallery ref="gallery" :imgs="largeImgs" v-show="showGallery" @close="handleGalleryClose"></app-gallery>
    </transition>
  </div>
</template>

<script>
import axios from 'axios'
import AppTitle from '@/components/Title/Title.vue'
import AppAlbum from '@/components/Album/Album.vue'
import AppGallery from '@/components/Gallery/Gallery.vue'
export default {
  name: 'app',
  components: {
    AppTitle,
    AppAlbum,
    AppGallery
  },
  data () {
    return {
      showGallery: false,
      albumImgs: [],
      largeImgs: []
    }
  },
  mounted () {
    axios.get('https://api.shanghaichujie.com/api/ali/oss/files/all', {
      params: {
        disk: 'h5-touch',
        path: 'project/abbott/'
      }
    }).then((res) => {
      let imgArr = res.data.list
      imgArr.forEach((item) => {
        this.albumImgs.push('https://h5-touch.oss-cn-shanghai.aliyuncs.com/' + item + '?x-oss-process=style/abbott')
        this.largeImgs.push('https://h5-touch.oss-cn-shanghai.aliyuncs.com/' + item)
      })
    })
  },
  methods: {
    handleAlbumClick (index) {
      this.$refs.gallery.slideTo(index)
      this.showGallery = true
    },
    handleGalleryClose () {
      this.showGallery = false
    }
  }
}
</script>

<style lang="scss">
html,
body {
  width: 100%;
  height: 100%;
}
#app {
  overflow: hidden;
  width: 100%;
  height: 100%;
  background: url("~@/assets/imgs/bg.jpg") no-repeat;
  background-size: 100% 100%;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>
