<template>
  <div class="gallery" @click="handleGalleryClick">
    <swiper :options="swiperOptions" ref="mySwiper">
      <swiper-slide v-for="(item, index) of imgs" :key="index">
        <img class="gallery-img" v-lazy="item">
      </swiper-slide>
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  props: {
    imgs: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      swiperOptions: {
        observer: true,
        observeParents: true,
        pagination: {
          el: '.swiper-pagination',
          type: 'fraction'
        }
      }
    }
  },
  methods: {
    handleGalleryClick () {
      this.$emit('close')
    },
    slideTo (index) {
      this.$refs.mySwiper.swiper.slideTo(index)
    }
  }
}
</script>

<style lang="scss" scoped>
.gallery >>> .swiper-container {
  overflow: inherit;
  width: 100%;
  height: 100%;
  .swiper-slide {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    .gallery-img {
      object-fit: cover;
      max-width: 100%;
      max-height: 80%;
    }
  }

  .swiper-pagination {
    bottom: 20px;
    font-size: 24px;
    color: #ffffff;
  }
}
.gallery {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 99;
  background: #000000;
}
</style>
