<template>
  <div ref="wrapper" class="wrapper">
    <ul class="album">
      <li v-for="(item, index) of imgs" :key="index" @click="handleAlbumClick(index)">
        <div><img v-lazy="item"></div>
      </li>
    </ul>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
export default {
  name: 'album',
  props: {
    imgs: {
      type: Array,
      default () {
        return []
      }
    }
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.wrapper, {
      click: true
    })
  },
  methods: {
    handleAlbumClick (index) {
      this.$emit('clickImg', index)
    }
  }
}
</script>

<style lang="scss" scoped>
.wrapper {
  overflow: hidden;
  position: absolute;
  top: 150px;
  left: 0;
  right: 0;
  bottom: 50px;
}
.album {
  overflow: hidden;
  li {
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 250px;
    text-align: center;
    div {
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 240px;
      height: 150px;
      background: #000;
      img {
        width: 100%;
      }
    }
  }
}
</style>
